<template>
	<div>
	 <el-row :gutter="12">
      <el-col :span="12">
        <el-card shadow="hover">
          <span id="block"><b>Mood</b></span>
          <div id="MoodIcons">
            <img src="@/assets/emoji/emoji_verygood.png">
            <img src="@/assets/emoji/emoji_good.png">
            <img src="@/assets/emoji/emoji_neutral.png">
            <img src="@/assets/emoji/emoji_bad.png">
            <img src="@/assets/emoji/emoji_verybad.png">
          </div>
          <div id="MoodChart"></div>
          <div id="MoodWords">
            <span></span> 
            <span></span>
          </div>
        </el-card>
        <el-card shadow="hover" class="second">
          <span><b>Keywords</b></span>
          <div id="keywords">
            
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card shadow="hover">
          <span><b>Entries</b></span>
          <div id="EntryChart"></div>
        </el-card>
        <el-card shadow="hover" class="second">
          <span><b>Cognitive Distortions</b></span>
        </el-card>
      </el-col>
    </el-row>
	</div>
</template>

<script>
  import axios from "axios"
  export default {
    data() {
      return {
        trends: '',
        patient: this.$store.state.patient,
        data:'',
        update: true
      }
    },
    computed: {
      patient_id() {
        return this.$store.state.patient_id
      }
    },
    updated() {
      var get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
              //then获取成功；response成功后的返回值（对象）
      .then(response=>{
         this.trends=response.data;
         this.drawChart(this.trends);
      })
              //获取失败
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    mounted() {
      var get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
              //then获取成功；response成功后的返回值（对象）
      .then(response=>{
         this.trends=response.data;
         this.drawChart(this.trends);
      })
              //获取失败
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    watch: {
      patient_id(oldval, newval){
        console.log(oldval);
        this.$forceUpdate();
      },
      '$route' (to, from) {
        var get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
        axios.get(get_path)
              //then获取成功；response成功后的返回值（对象）
      .then(response=>{
         this.trends=response.data;
         if (this.trends != null) {
          this.drawChart(this.trends);
         }
      })
              //获取失败
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
      }
    },
    methods: {
      reload() {
        this.$forceUpdate()
      },
      drawChart(raw) {
        console.log(raw)
        var mood_list = []
        var entries = [0,Object.keys(raw).length]
        raw.forEach(el => {
          mood_list.push(el.mood)
        })
        let moodChart = this.$echarts.init(document.getElementById('MoodChart'))
        let entryChart = this.$echarts.init(document.getElementById('EntryChart'))
        moodChart.setOption({
          grid: {
            top: 40,
            left: 20,
            right: 20
          },
          xAxis: {
            show: false,
            data: ['1','2','3','4','5','6'],
          },
          yAxis: {
            min: 1,
            max: 5,
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ['#686A7F', '#ACADB6', '#2BDCB5', '#AADD01', '#FFA92B'],
                type: 'dashed'
              }
            }
          },
          series: [{
            data: mood_list,
            type: 'scatter'
          }]
        })
        entryChart.setOption({
          xAxis: {
            type: 'category',
            data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
          },
          yAxis: {
            type: 'value',
            minInterval: 1,
          },
          series: [{
            data: entries,
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#00577E'
              }
            }
          }]
        })
      }
    }
  }
</script>

<style>
  .second {
    margin-top: 20px;
  }
  #block {
    display: block;
  }
  #MoodIcons {
    margin-top: 24px;
    display: inline-block;
    width: 10%;
    height: 275px;
  }
  #MoodChart {
    display: inline-block;
    float: right;
    width: 90%; 
    height: 300px;
  }
  #MoodIcons img {
    height: 36px;
    margin-bottom: 7px;
    margin-left: 20px;
  }
  #EntryChart {
    width: 100%; 
    height: 300px;
  }
</style>



